@import url('https://fonts.googleapis.com/css?family=Roboto:700');

body {
  background: linear-gradient(135deg, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.7), rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 1));
  background-size: 400% 400%;
}

* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

#logo {
  width: 300px;
}
.navbar-links {
  list-style-type: none;
  margin-left: auto; 
  display: flex;
  gap: 20px;
}
.navbar-nav {
  margin-left: auto;
}
.navbar-links li {
  display: inline-block;
}

.nav-link {
  text-decoration: none; 
  color: #fff; 
  padding: 10px; 
}

.nav-link:hover {
  color: #ddd; 
}

#avatar {
  border-radius: 20px;
  width: 100%;
}
.basicInfo {
  display: flex;
  padding-top: 50px;
  width: 800px;
}

.resume{
background: white;
}

.trainings {
  p {
    padding: 10px;
  }
}

i:hover {
  text-decoration: underline;
}

.hello {
  text-shadow: 0 0 7px rgba(255,255,255,.3), 0 0 3px rgba(255,255,255,.3);
}

.basic {
  color: #e5e5e5;
  font-size: 2.26rem;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
}

.animation {
  height:50px;
  overflow:hidden;
  margin-left: 1rem;
  text-transform: uppercase;
}

.animation > div > div {
  padding: 0.25rem 0.75rem;
  height:2.81rem;
  margin-bottom: 2.81rem;
  display:inline-block;
}

.animation div:first-child {
  animation: text-animation 8s infinite;
}

.first div {
  font-weight: 600;
}
.second div {
  font-weight: 600;
}
.third div {
  font-weight: 600;
}

@keyframes text-animation {
  0% {margin-top: 0;}
  10% {margin-top: 0;}
  20% {margin-top: -5.62rem;}
  30% {margin-top: -5.62rem;}
  40% {margin-top: -11.24rem;}
  60% {margin-top: -11.24rem;}
  70% {margin-top: -5.62rem;}
  80% {margin-top: -5.62rem;}
  90% {margin-top: 0;}
  100% {margin-top: 0;}
}

.footer {
  padding: 20px;
  background-color: #ced4da;
  text-align: left;
  li {
    list-style: none;
  }
  #logo2 {
    width: 200px;
    padding-left: 30px;
    
  }
}

.page-link {
  border: none;
  margin: 10px;
}

.logosec {
  img {
    padding: 5px;
    width: 75px;
  }
}

@media (max-width: 768px) {
  .col-3, .col-5 {
    width: 100%;
  }
  .col-5 > .basicInfo {
    display: block;
    text-align: center;
    width: 100%;
    .hello, .animation {
      width: 100%;
    }
  }

  .footer{
    .row {
      display: block !important;
      .col-4 {
        width: 100%;
        padding-bottom: 20px;
        text-align: center;
      }
    }
  }
  .p-3 > .topContent {
    display: block !important;
  }
  .education, .trainings {
    display: block !important;
    text-align: center;
  }
}